import React, { useState, useCallback, useEffect, Fragment } from "react";
import { useSelector, useDispatch } from "react-redux";
import { useTranslation } from "react-i18next";
import * as actions from "../../../store/actions";
import { Typography, Grid, Paper, Box, Button } from "@material-ui/core";
import TableSkeleton from "../../../components/common/TableSkeleton";
import AddRoundedIcon from "@material-ui/icons/AddRounded";
import EditRoundedIcon from "@material-ui/icons/EditRounded";
import ButtonSpinner from "../../../components/UI/Spinners/ButtonSpinner";
import { isError } from "../../../util/generals";
import Input from "../../../components/Inputs/Input";
import IosSwitch from "../../../components/Inputs/IosSwitch";
import MoblieInput from "../../../components/Inputs/MoblieInput";
import SelectInput from "../../../components/Inputs/SelectInput";

const initialState = (admin) => {
  return {
    firstname: {
      value: admin ? admin.firstname : "",
      validity: admin ? !!admin.firstname : false,
    },
    lastname: {
      value: admin ? admin.lastname : "",
      validity: admin ? !!admin.lastname : false,
    },
    email: {
      value: admin ? admin.email : "",
      validity: admin ? !!admin.email : false,
    },
    country_code: {
      value: admin ? admin.country_code + "" : "966",
      validity: admin ? !!admin.country_code : true,
    },
    mobile: {
      value: admin ? admin.mobile : "",
      validity: admin ? !!admin.mobile : false,
    },
    role: {
      value: admin ? admin.role : "Administrator",
      validity: admin ? !!admin.role : true,
    },
    status: {
      value: admin ? admin.status : "1",
      validity: true,
    },
  };
};

let timeout;

const AddEdit = ({ history, match }) => {
  const isEdit = match.params.id;
  const [editLoading, setEditLoading] = useState(!!isEdit);
  const [state, setState] = useState(initialState());
  const [touched, setTouched] = useState(false);
  const admins = useSelector((state) => state.admins);
  const { t } = useTranslation();
  const dispatch = useDispatch();

  const fetchAdmin = useCallback(async () => {
    try {
      const fetchedAdmin = await dispatch(actions.getAdmin(isEdit));
      setState(initialState(fetchedAdmin));
      setEditLoading(false);
    } catch (err) {
      let msg = err.message || t("settings:admin_not_found");
      dispatch(actions.fireSnake("error", msg));
      history.replace("/settings/admins");
    }
  }, [isEdit, dispatch, t, history]);

  useEffect(() => {
    if (isEdit) {
      timeout = setTimeout(() => {
        fetchAdmin();
      }, 0);
    }
    return () => clearTimeout(timeout);
  }, [isEdit, fetchAdmin]);

  const handleState = (value, validity, name) => {
    setState((prev) => {
      return {
        ...prev,
        [name]: { value: value, validity: validity },
      };
    });
  };

  const handleSubmit = async () => {
    let body = {};
    // validate non-nested state
    for (const key in state) {
      body = { ...body, [key]: state[key].value };
      if (!state[key].validity) {
        setTouched(true);
        return dispatch(
          actions.fireSnake("error", t("common:complete_red_input"))
        );
      }
    }

    try {
      await dispatch(actions.addEditAdmin(body, isEdit));
      history.replace("/settings/admins");
    } catch (err) {
      let msg = err.message || t("settings:save_admin_err");
      dispatch(actions.fireSnake("error", msg));
    }
  };

  return (
    <Fragment>
      <Typography align="center" variant="h4" className="py-1">
        {isEdit ? t("settings:edit_admin") : t("settings:add_admin")}
      </Typography>
      {editLoading ? (
        <Paper>
          <TableSkeleton row={4} col={2} />
        </Paper>
      ) : (
        <Fragment>
          <Paper className="my-1 p">
            <Box className="flex flex-justify-between align-items-center py">
              <Typography align="left" variant="h6" className="p">
                {t("settings:admin_detail")}
              </Typography>
              <IosSwitch
                state={state.status.value}
                onChange={handleState}
                name="status"
              />
            </Box>
            <Grid container spacing={1}>
              {/* Admin Info */}
              <Grid item md={4} sm={6} xs={12}>
                <Input
                  label={t("common:firstname") + " **"}
                  initialValue={state.firstname.value}
                  name="firstname"
                  getResult={handleState}
                  required
                  min={2}
                  max={11}
                  setTouched={touched}
                  serverError={isError(
                    admins.errors && admins.errors.data,
                    `firstname`
                  )}
                />
              </Grid>
              <Grid item md={4} sm={6} xs={12}>
                <Input
                  label={t("common:lastname") + " **"}
                  initialValue={state.lastname.value}
                  name="lastname"
                  getResult={handleState}
                  required
                  min={2}
                  max={11}
                  setTouched={touched}
                  serverError={isError(
                    admins.errors && admins.errors.data,
                    `lastname`
                  )}
                />
              </Grid>
              <Grid item md={4} sm={6} xs={12}>
                <Input
                  label={t("common:email") + " **"}
                  initialValue={state.email.value}
                  name="email"
                  getResult={handleState}
                  required
                  email
                  setTouched={touched}
                  serverError={isError(
                    admins.errors && admins.errors.data,
                    `email`
                  )}
                />
              </Grid>

              <Grid item md={4} sm={6} xs={12}>
                <MoblieInput
                  reducer={admins}
                  countryCode={state.country_code.value}
                  mobile={state.mobile.value}
                  onChange={handleState}
                  touched={touched}
                />
              </Grid>
              <Grid item md={4} sm={6} xs={12}>
                <SelectInput
                  options={[
                    { text: t("settings:Owner"), value: "Owner" },
                    {
                      text: t("settings:Administrator"),
                      value: "Administrator",
                    },
                    { text: t("settings:Manager"), value: "Manager" },
                    { text: t("settings:Seller"), value: "Seller" },
                  ]}
                  name="role"
                  label={t("settings:role")}
                  getResult={handleState}
                  initialValue={state.role.value}
                  serverError={isError(
                    admins.errors && admins.errors.data,
                    "role"
                  )}
                />
              </Grid>
            </Grid>
          </Paper>
          {/* Submit */}
          <Box className="center py-1">
            <Button
              variant="contained"
              color="primary"
              size="large"
              onClick={handleSubmit}
              endIcon={admins.itemLoading && <ButtonSpinner />}
              startIcon={isEdit ? <EditRoundedIcon /> : <AddRoundedIcon />}
              disabled={admins.itemLoading}
              style={{ width: "80%" }}
            >
              <Typography variant="h4" className="py-1">
                {isEdit ? t("settings:edit_admin") : t("settings:add_admin")}
              </Typography>
            </Button>
          </Box>
        </Fragment>
      )}
    </Fragment>
  );
};

export default AddEdit;
