import { Box } from "@material-ui/core";
import React from "react";
import { useTranslation } from "react-i18next";
import countries from "../../util/countries";
import { isError } from "../../util/generals";
import Input from "./Input";
import SelectInput from "./SelectInput";

const _countries = countries.map((c, i) => {
  return {
    id: `${c.code}_${c.dial_code}_${i}`,
    text: c.flag + c.dial_code,
    value: c.dial_code.substring(1, c.dial_code.length),
  };
});

const MoblieInput = ({ reducer, countryCode, mobile, onChange, touched }) => {
  const { t } = useTranslation();
  return (
    <Box className="flex ltr">
      <Box>
        <SelectInput
          options={_countries}
          name="country_code"
          //   label={t("common:country_code")}
          getResult={onChange}
          initialValue={countryCode}
          serverError={isError(
            reducer.errors && reducer.errors.data,
            "country_code"
          )}
          styles={{ borderRadius: "3px 0 0 3px", direction: "ltr" }}
        />
      </Box>
      <Box className="fullwidth">
        <Input
          label={t("common:mobile") + " **"}
          initialValue={mobile}
          name="mobile"
          getResult={(value, validity, name) => {
            let val = value;
            if (val && +val.charAt(0) === 0) {
              val = val.substring(1, val.length);
            }
            onChange(val, validity, name);
          }}
          required
          decimal
          setTouched={touched}
          serverError={isError(reducer.errors && reducer.errors.data, `mobile`)}
          fieldStyles={{ borderRadius: "0 3px 3px 0" }}
        />
      </Box>
    </Box>
  );
};

export default MoblieInput;
